@import url(https://fonts.googleapis.com/css?family=Noto+Sans:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top-links {
  display: none;
}

@media (min-width: 768px) {
  .en-flag {
    vertical-align: middle;
  }

  .leftnav {
    flex-grow: 1;
  }

  .rightnav {
    padding-right: 15px;
    margin-left: 5px;
    margin-right: 20px;
  }

  .safety-icon {
    vertical-align: middle;
  }

  .shopping-cart-icon {
    background-color: var(--primary);
  }

  .shopping-cart-li {
    background-color: var(--primary);
  }

  .top-links {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    list-style-type: none;
    align-items: center;
    background-color: black;
    color: white;
    padding: 5px;
    font-size: 14px;
    margin-top: 0;
  }
}
.main-nav {
  display: none;
}

.mobile-header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: stretch;
  align-items: center;
  list-style-type: none;
  padding: 15px;
}

.mobile-header.hidden {
  display: none;
}

.mobile-header-child {
  order: 0;
  flex: 1 1 auto;
  align-self: auto;
  width: 200px;
  margin-left: 10px;
}

@media (min-width: 768px) {
  .large-logo {
    max-width: 150px;
    float: left;
  }

  .main-links {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    align-items: center;
    border-bottom: solid 2px var(--borders);
    padding: 0px 10px 20px 0px;
    font-size: 20px;
  }

  .main-nav {
    display: inline;
  }

  .main-nav__link {
    margin-left: 40px;
    font-size: 20px;
  }

  .mobile-header {
    display: none;
  }

  .solutions-link {
    text-decoration: none;
    color: var(--dark-text);
  }

  .solutions-link.open {
    color: var(--primary);
    border-bottom: solid 2px var(--primary);
  }


  #last-link {
    flex-grow: 1;
  }

  #search {
    padding: 20px;
    border: solid 1px var(--borders);
    border-radius: 5px;
    width: 300px
  }
}
.drop-down {
  display: none;
}

.drop-down.open {
  display: block;
  margin-top: 40px;
}

.solutions__links {
  padding: 20px;
}

li {
  list-style-type: none;
}
.chevron-right {
  float: right;
}

.lower-nav {
  background-color: var(--page-section-backgrounds);
  padding-bottom: 100%;
}

.lower-nav__link {
  margin-bottom: 15px;
}

.lower-nav-ul {
  margin-top: 0px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  padding-left: 15px;
  padding-top: 15px;
}

.menu-div {
  padding: 5px 15px 5px 15px;
  background-color: black;
  color: white;
}

.mobile-nav {
  text-decoration: none;
  color: var(--dark-text);
}

.mobile-nav__link {
  padding: 15px;
  border-bottom: solid 1px var(--borders);
}

.question-circle, .map-marker-alt {
  margin-right: 5px;
}

.times {
  float: right;
  color: white;
  vertical-align: middle;
}
.chevron-right {
  float: right;
}

.mobile-nav-link {
  text-decoration: none;
  color: var(--dark-text);
}

.mobile-solutions__link {
  padding: 15px;
  border-bottom: solid 1px var(--borders);
}
* {
  box-sizing: border-box;
}

:root {
  --primary: #b81830;
  --dark-text: #181818;
  --borders: #d0d0d0;
  --lightest-text: #666666;
  --page-section-backgrounds: #f4f4f4;
}

body {
  font-family: 'Noto Sans', sans-serif;
}



